<template>
    <div id="cartlist">
        <div class="cart_title" v-show="!info_editable">{{title}}</div>
        <div class="cart_title" v-show="info_editable"><input class="cart_title" v-model="title"></div>
        <div  class="cart_info" v-show="!info_editable">
            <div class="cart_dead_time">截止: {{dead_time}}</div>
            <div class="cart_sponsor">Sponsor: {{sponsor}}</div>
        </div>
        <div  class="cart_info" v-show="info_editable">
            <div class="cart_dead_time">截止: <input v-model="dead_time"></div>
            <div class="cart_sponsor">Sponsor: <input v-model="sponsor"></div>
        </div>
        <div>
            <button @click="edit_info">{{info_editable_btn_text}}</button>
            <button>刪除團購</button>
        </div>
        <div class="cart_participants">
            <div v-for="(p, index) in participants" :key="index" class="cart_participant">
                <div v-show="p.participant_editable">
                    <div>
                        參團人: <input v-model="p.buyer">
                    </div>
                    <div>
                        團購品: <input v-model="p.product">
                    </div>
                    <div>
                        數　量: <input v-model="p.quantity">
                    </div>
                </div>
                <div v-show="!p.participant_editable">
                    {{p.buyer}}:  {{p.product}} x {{p.quantity}}
                </div>
                <div class="cart_participant_func">
                    <button @click="edit_participant(index)">{{p.participant_editable_btn_text}}</button>
                    <button @click="delete_participant(index)">刪除</button>
                </div>
            </div>
        </div>
        <button @click="add_participant">新增團員</button>
    </div>
</template>

<script>
export default {
    data () {
        return {
            title: '防寫隨身碟團購',
            dead_time: '2020-03-31 12:00',
            sponsor: 'Terry.D',
            info_editable: false,
            info_editable_btn_text: '編緝資訊',
            participants: [
                {
                    buyer: 'Terry.D',
                    product: '防寫隨身碟',
                    quantity: 1,
                    participant_editable: false,
                    participant_editable_btn_text: '修改'
                },
                {
                    buyer: 'Ling',
                    product: '',
                    quantity: 1,
                    participant_editable: false,
                    participant_editable_btn_text: '修改'
                }
            ]
        }
    },
    methods: {
        edit_info: function () {
            this.info_editable = !this.info_editable
            this.info_editable_btn_text = (this.info_editable == false) ? '編緝資訊' : '確定'
        },
        add_participant: function() {
            this.participants.push({
                buyer: '',
                product: '',
                quantity: 1,
                participant_editable: true,
                participant_editable_btn_text: '新增'
            })
        },
        edit_participant: function (index) {
            if (this.participants[index].buyer == '') {
                this.participants.splice(index)
            } else {
                this.participants[index].participant_editable = !this.participants[index].participant_editable
                this.participants[index].participant_editable_btn_text = (this.participants[index].participant_editable == false) ? '修改' : '確定'
            }
        },
        delete_participant: function (index) {
            this.participants.splice(index, 1)
        }
    }
}
</script>

<style scoped>
.cart_title {
    font-size: 30px;
    font-weight: bold;
}

.cart_info {
    width: 350px;
    /* border-top: 1px solid gray; */
    /* border-bottom: 1px solid gray; */
    margin: 20px auto;
}

.cart_dead_time {
    color: blue;
    text-align: right;
    margin: 10px 10px;
}

.cart_sponsor {
    text-align: right;
    margin: 10px 10px;
}

.cart_participants {
    width: 350px;
    border-top: 1px solid gray;
    margin: 20px auto;
}

.cart_participant {
    border-bottom: 1px solid gray;
    padding: 20px 20px;
}

.cart_participant_func {
    margin: 10px 10px;
}
</style>


